$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.bBlocksProModal {
	.components-modal__content {
		>*:last-child {
			max-width: 100%;
		}

		.features {
			.item {
				display: flex;
				align-items: center;
				gap: 9px;
				font-size: 15px;
				font-weight: 400;
				color: #000000d1;
			}
		}
	}

	.components-modal__header-heading {
		display: flex;
		align-items: center;
		gap: 25px;

		.upgradeNow {
			margin: 0;
			padding: 8px 15px;
			border-radius: 5px;
			text-align: center;
		}
	}

	h3 {
		margin: 0 0 15px 0;

		span {
			color: $primaryColor;
		}
	}

	.text {
		font-size: 15px;
		margin: 25px 0 0 0;

		span {
			color: $primaryColor;
		}
	}

	.upgradeNow {
		display: inline-block;
		font-size: 16px;
		font-weight: 500;
		color: #fff;
		background-color: #4527a4;
		text-decoration: none;
		padding: 10px 10px;
		margin-top: 20px;
		border-radius: 3px;
		width: 18%;
		white-space: nowrap;
		text-align: center;

		&:hover,
		&:focus {
			color: #fff;
			background-color: $secondaryColor;
		}
	}
}